<template>
  <div class="farm" v-loading="loading">
    <el-row style="margin-top: 20px; margin-bottom: 20px;">
      <el-col>
        <el-breadcrumb separator-class="mdi mdi-chevron-right">
          <el-breadcrumb-item :to="{path: '/dashboard'}">Home</el-breadcrumb-item>
          <el-breadcrumb-item>{{farm.name}}</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <div class="farm-header">
      <el-row>
        <el-col>
          <h2>{{farm.name}}</h2>
        </el-col>
      </el-row>
      <el-row justify="space-around">
        <el-col :span="3">
          <el-card>
            <template #header>
              <div class="header-cards-title">
                <span>Worker</span>
              </div>
            </template>
            <div class="header-cards-body">
              <span><span class="mdi mdi-pickaxe"></span>{{onlineWorkers}}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="3">
          <el-card>
            <template #header>
              <div class="header-cards-title">
                <span>GPUs</span>
              </div>
            </template>
            <div class="header-cards-body">
              <span><span class="mdi mdi-cpu-64-bit"></span>{{totalGpus}}</span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="3">
          <el-card>
            <template #header>
              <div class="header-cards-title">
                <span>Consumption</span>
              </div>
            </template>
            <div class="header-cards-body">
              <span><span class="mdi mdi-lightning-bolt"></span>{{totalPower}}W</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-tabs v-model="activeTabs">
      <el-tab-pane label="Workers" name="Workers">
        <el-empty v-if="farm.workers?.length === 0" description="Aucun worker ajout  ce farm"></el-empty>
        <el-row v-for="(worker, index) in farm.workers" :key="worker" class="row-workers">
          <el-col>
            <el-collapse accordion v-model="activeCollapse">
              <el-collapse-item :name="index">
                <template #title>
                  <el-space>
                    <RoundStatus :type="workerStatus(index)"/>
                    <router-link :to="'/farm/' + this.$route.params.id + '/' + worker.id" v-slot="{href, navigate, isActive}">
                      <el-link :class="{active: isActive}" :href="href" @click.stop="navigate">Rig #{{worker.id}} ({{worker.name || 'undefined'}})</el-link>
                    </router-link>
                    <el-popover placement="top-start" title="GPUs" trigger="hover">
                      <template #reference>
                        <el-tag size="mini" type="success" class="tag-worker">{{workerTotalGpu(index)}} GPUs</el-tag>
                      </template>
                      <span v-html="workerGpusName(index)"></span>
                    </el-popover>
                    <el-tag size="mini" type="warning" class="tag-worker">{{workerPower(index)}}W <span class="mdi mdi-lightning-bolt"></span></el-tag>
                    <el-tag size="mini" type="primary" class="tag-worker">{{workerHashRate(index)}} <span class="mdi mdi-pickaxe"></span></el-tag>
                  </el-space>
                </template>
                <el-descriptions title="Rig informations" size="medium" :column="3" border v-if="worker.hardwares != null">
                  <el-descriptions-item>
                    <template #label>
                      <span class="mdi mdi-chip"></span>
                      CPU
                    </template>
                    {{worker.hardwares.cpu.model}}
                    <el-tag type="success" size="mini" v-if="worker.hardwares.cpu.aes === '1'">AES</el-tag>
                  </el-descriptions-item>
                  <el-descriptions-item>
                    <template #label>
                      <span class="mdi mdi-passport-biometric"></span>
                      Motherboard
                    </template>
                    {{worker.hardwares.mb.manufacturer}} {{worker.hardwares.mb.product}} ({{worker.hardwares.mb.bios}})
                  </el-descriptions-item>
                  <el-descriptions-item>
                    <template #label>
                      <span class="mdi mdi-harddisk"></span>
                      Disk
                    </template>
                    {{worker.hardwares.disk_model}}
                  </el-descriptions-item>
                  <el-descriptions-item>
                    <template #label>
                  <span class="green-desc">
                    <span class="mdi mdi-expansion-card"></span>
                    GPUs
                  </span>
                    </template>
                    <div v-for="gpu in worker.hardwares.gpu" :key="gpu">
                      {{gpu.subvendor}} {{gpu.name}} - {{gpu.mem}} {{gpu.mem_type}}
                    </div>
                  </el-descriptions-item>
                  <el-descriptions-item>
                    <template #label>
                      <span class="mdi mdi-wan"></span>
                      Addresses
                    </template>
                    <div v-for="ip in worker.hardwares.ip" :key="ip">
                      {{ip}}
                    </div>
                  </el-descriptions-item>
                </el-descriptions>
                <el-empty v-else description="No hardware informations"></el-empty>
              </el-collapse-item>
            </el-collapse>
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <BannerButton type="primary" :loading="newWorkerBtnLoading" @click="generateNewWorker"><span class="mdi mdi-plus" style="font-size: 1.3rem;"></span></BannerButton>
          </el-col>
        </el-row>
        <el-dialog v-model="newWorkerDialogVisible" title="Nouveau worker">
          <p>ID:</p>
          <h3>{{this.newWorkerInfos.id}}</h3>
          <p>Password:</p>
          <p>{{this.newWorkerInfos.pwd}}</p>
          <template #footer>
        <span class="dialog-footer">
          <el-button @click="newWorkerDialogVisible = false">Fermer</el-button>
        </span>
          </template>
        </el-dialog>
      </el-tab-pane>
      <el-tab-pane label="Wallets" name="Wallets">
        <Wallets :wallets="wallets" @saveWallet="saveNewWallet" @deleteWallet="deleteWallet"/>
      </el-tab-pane>
      <el-tab-pane label="Flightsheets" name="Flightsheets">
        <Flightsheet :flightsheets="flightsheets" :wallets="wallets" @added="this.getFlightsheets" @deleted="this.getFlightsheets"/>
      </el-tab-pane>
      <el-tab-pane label="Overclocking template" name="oc">
        <OCTemplate :ocs="ocs"/>
      </el-tab-pane>
      <el-tab-pane label="Settings" name="settings">
        <el-row>
          <el-col :span="12" :xs="24">
            <h3>Name</h3>
          </el-col>
          <el-col :span="12" :xs="24">
            <el-input v-model="farm.name"/>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" :xs="24">
            <h3>Farm Hash</h3>
          </el-col>
          <el-col :span="12" :xs="24">
            <el-input v-model="farm._id.$oid"/>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" :xs="24">
            <h3>Timozone</h3>
          </el-col>
          <el-col :span="12" :xs="24">
            <el-select v-model="farm.timezone" filterable>
              <el-option-group v-for="(group, idx) in timezones" :key="idx" :label="idx">
                <el-option v-for="item in group" :key="item" :label="item" :value="item"/>
              </el-option-group>
            </el-select>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" :xs="24">
            <h3>Remove farm</h3>
          </el-col>
          <el-col :span="12" :xs="24">
            <el-popconfirm confirm-button-text="Yes" cancel-button-text="No"
                           icon="mdi mdi-info-filled" icon-color="red" title="Are you sure to delete this farm ?">
              <template #reference>
                <el-button type="danger">Remove farm</el-button>
              </template>
            </el-popconfirm>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import api from "../api/antos-api";
import BannerButton from "../components/BannerButton";
import RoundStatus from "../components/RoundStatus";
import Wallets from "@/components/Wallets";
import Flightsheet from "@/components/Flightsheet";
import utils from "@/utils/utils";
import wapi from "../api/weather-api"
import OCTemplate from "@/components/OCTemplate";
export default {
  name: "Farm",
  data() {
    return {
      farm: {
        _id: {
          $oid: ""
        }
      },
      wallets: [],
      flightsheets: [],
      loading: true,
      newWorkerDialogVisible: false,
      newWorkerBtnLoading: false,
      newWorkerInfos: {
        id: -1,
        pwd: ''
      },
      activeCollapse: '1',
      activeTabs: 'Workers',
      timezones: {},
      ocs: []
    }
  },
  mounted() {
    api.get_farm(this.$route.params.id).then(resp => {
      console.log(resp.data)
      this.farm = resp.data
      this.loading = false
    })
    api.get_farm_wallets(this.$route.params.id).then(resp => {
      this.wallets = resp.data
    })
    this.getFlightsheets()
    api.get_farm_oc_templates(this.$route.params.id, "").then(resp => {
      this.ocs = resp.data
    })
    wapi.get_timezones().then(resp => {
      var temp = resp.data.map(value => value.split('/'))
      temp.forEach(value => {
        this.timezones[value[0]] ? this.timezones[value[0]].push(value[1]) : this.timezones[value[0]] = [value[1]]
      })
    })
  },
  components: {
    OCTemplate,
    Flightsheet,
    Wallets,
    BannerButton,
    RoundStatus
  },
  computed: {
    onlineWorkers() {
      return this.farm.workers?.filter(worker => utils.isWorkerOnline(worker)).length
    },
    totalGpus() {
      return this.farm.workers?.filter(worker => utils.isWorkerOnline(worker))
          .map(worker => worker.hardwares?.gpu.length || 0).reduce((curr, acc) => curr + acc, 0)
    },
    totalPower() {
      return this.farm.workers?.filter(worker => utils.isWorkerOnline(worker))
          .map(worker => (worker.stats?.power ||[0]).reduce((curr, acc) => curr + acc)).reduce((curr, acc) => curr + acc, 0)
    }
  },
  methods: {
    getFlightsheets() {
      api.get_farm_flightsheet(this.$route.params.id).then(resp => {
        this.flightsheets = resp.data
      })
    },
    workerStatus(index) {
      return utils.isWorkerOnline(this.farm.workers[index]) ? 'success' : 'error'
    },
    workerPower(index) {
      return this.farm.workers[index].stats?.power.reduce((curr, acc) => curr + acc, 0) | 0
    },
    workerTotalGpu(index) {
      return this.farm.workers[index].hardwares?.gpu.length | 0
    },
    workerHashRate(index) {
      let khs = this.farm.workers[index].stats?.total_khs | 0
      let suffix = 'kh/s'
      if(khs >= 1000) {
        khs /= 1000
        suffix = 'mh/s'
      }
      return khs.toFixed(2) + suffix
    },
    workerGpusName(index) {
      return this.farm.workers[index].hardwares?.gpu.map(value => value.name).reduce((curr, acc) => curr + "<br/>" + acc)
    },
    generateNewWorker() {
      this.newWorkerBtnLoading = true
      api.post_worker(this.farm._id.$oid).then(resp => {
        this.newWorkerInfos = resp.data
        this.newWorkerBtnLoading = false
        this.newWorkerDialogVisible = true
      })
    },
    saveNewWallet(form) {
      api.post_farm_wallet(this.farm._id.$oid, form).then(() => {
        this.$router.go()
      })
    },
    deleteWallet(address) {
      api.delete_farm_wallet(address).then(() => {
        this.$router.go()
      })
    }
  }
}
</script>

<style scoped>
.row-workers {
  margin-bottom: 16px;
}

.green-desc {
  color: orange;
}

.farm-header {
  background-color: #F8F0DF;
  padding-top: 10px;
}

h2 {
  font-size: 2rem;
}

.container {
  margin-left: 15%;
  margin-right: 15%;
  padding: 24px;
}

.header-cards-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.header-cards-body {
  font-size: 1.2rem;
}
</style>