<template>
  <div class="oc_templates">
    <el-row justify="end" class="newBtnRow">
      <el-col :span="3" :xs="24">
        <el-button type="primary" @click="formVisibile = true"><span class="mdi mdi-plus"></span> New</el-button>
      </el-col>
    </el-row>
    <el-empty v-if="ocs.length === 0" description="No data found"/>
    <div v-else>
      <el-row class="oc-row" v-for="(item, idx) in ocs" :key="item">
        <el-col :span="21">
          <el-row style="align-items: center">
            <el-col :span="3">
              <h4>{{item.config.name}}</h4>
            </el-col>
            <el-col :span="21">
              <el-row>
                <el-col>
                  <el-space :size="40">
                    <div v-if="item.config.nvidia.cco !== ''"><el-tag size="small" type="success">CORE</el-tag> {{item.config.nvidia.cco}}</div>
                    <div v-if="item.config.nvidia.mc !== ''"><el-tag size="small" type="success">MEM</el-tag> {{item.config.nvidia.mc}}</div>
                    <div v-if="item.config.nvidia.fan !== ''"><el-tag size="small" type="success">FAN</el-tag> {{item.config.nvidia.fan}}</div>
                    <div v-if="item.config.nvidia.pw !== ''"><el-tag size="small" type="success">PL</el-tag> {{item.config.nvidia.pw}}</div>
                  </el-space>
                </el-col>
              </el-row>
              <el-row>
                <el-col>
                  <el-space :size="40">
                    <div v-if="item.config.amd.cc !== ''"><el-tag size="small" type="danger">CORE</el-tag> {{item.config.amd.cc}}</div>
                    <div v-if="item.config.amd.mc !== ''"><el-tag size="small" type="danger">MEM</el-tag> {{item.config.amd.mc}}</div>
                    <div v-if="item.config.amd.fan !== ''"><el-tag size="small" type="danger">FAN</el-tag> {{item.config.amd.fan}}</div>
                    <div v-if="item.config.amd.pw !== ''"><el-tag size="small" type="danger">PL</el-tag> {{item.config.amd.pw}}</div>
                    <div v-if="item.config.amd.cv !== ''"><el-tag size="small" type="danger">VDD</el-tag> {{item.config.amd.cv}}</div>
                    <div v-if="item.config.amd.mv !== ''"><el-tag size="small" type="danger">MVDD</el-tag> {{item.config.amd.mv}}</div>
                    <div v-if="item.config.amd.mcv !== ''"><el-tag size="small" type="danger">VDDCI</el-tag> {{item.config.amd.mcv}}</div>
                    <div v-if="item.config.amd.ms !== ''"><el-tag size="small" type="danger">MDPM</el-tag> {{item.config.amd.ms}}</div>
                  </el-space>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <small>{{item.desc}}</small>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="1">
          <el-button type="primary" plain v-if="launchEnabled" @click="$emit(item.inUse ? 'stopped' : 'launched', item)"><span :class="item.inUse ? 'mdi mdi-stop' : 'mdi mdi-play'"></span></el-button>
        </el-col>
        <el-col :span="1">
          <el-button type="primary" @click="() => edit(idx)"><span class="mdi mdi-account-edit"></span></el-button>
        </el-col>
        <el-col :span="1">
          <el-button type="danger"><span class="mdi mdi-trash-can"></span></el-button>
        </el-col>
      </el-row>
    </div>
    <el-dialog title="Add new overclocking template" v-model="formVisibile">
      <el-form label-position="top" class="oc_form">
        <el-form-item label="Name" required>
          <el-input placeholder="Enter name here" v-model="form.name"/>
        </el-form-item>
        <el-form-item label="Card Types">
          <el-switch v-model="form.isNvidia" active-text="NVIDIA" inactive-text="AMD" active-color="#7AB547" inactive-color="#ED1C24"/>
        </el-form-item>
      </el-form>
      <el-form label-position="top" class="oc_form" v-if="!form.isNvidia">
        <el-form-item label="Core Clock Offset (MHz)">
          <el-input placeholder="Enter core clock offset here" v-model="form.amd.cco"/>
        </el-form-item>
        <el-form-item label="Core Clock (MHz)">
          <el-input placeholder="Enter core clock here" v-model="form.amd.cc"/>
        </el-form-item>
        <el-form-item label="Core State (index)">
          <el-input placeholder="Enter core state here" v-model="form.amd.cs"/>
        </el-form-item>
        <el-form-item label="Core Voltage (mV)">
          <el-input placeholder="Enter core voltage here" v-model="form.amd.cv"/>
        </el-form-item>
        <el-form-item label="Memory Controller Voltage (mV)">
          <el-input placeholder="Enter memory controller voltage here" v-model="form.amd.mcv"/>
        </el-form-item>
        <el-form-item label="Memory Clock (Mhz)">
          <el-input placeholder="Enter memory clock here" v-model="form.amd.mc"/>
        </el-form-item>
        <el-form-item label="Mem State (index)">
          <el-input placeholder="Enter mem state here" v-model="form.amd.ms"/>
        </el-form-item>
        <el-form-item label="Memory Voltage (mV)">
          <el-input placeholder="Enter memory voltage here" v-model="form.amd.mv"/>
        </el-form-item>
        <el-form-item label="Fan (%)">
          <el-input placeholder="Enter fan here" v-model="form.amd.fan"/>
        </el-form-item>
        <el-form-item label="Power Limit (W)">
          <el-input placeholder="Enter power limit here" v-model="form.amd.pw"/>
        </el-form-item>
        <el-form-item label="SoC Frequency (Mhz)">
          <el-input placeholder="Enter SoC frequency here" v-model="form.amd.soc_f"/>
        </el-form-item>
        <el-form-item label="SoC VDDmax (mV)">
          <el-input placeholder="Enter SoC VDDmax here" v-model="form.amd.vddmax"/>
        </el-form-item>
        <el-form-item label="Aggressive undervolting (set OC for each DPM state)">
          <el-switch v-model="form.amd.aggressive_undervolting"/>
        </el-form-item>
      </el-form>
      <el-form label-position="top" class="oc_form" v-if="form.isNvidia">
        <el-form-item label="Core Clock Offset (MHz)">
          <el-input placeholder="Enter core clock offset here" v-model="form.nvidia.cco"/>
        </el-form-item>
        <el-form-item label="Memory Clock (Mhz)">
          <el-input placeholder="Enter memory clock here" v-model="form.nvidia.mc"/>
        </el-form-item>
        <el-form-item label="Fan (%)">
          <el-input placeholder="Enter fan here" v-model="form.nvidia.fan"/>
        </el-form-item>
        <el-form-item label="Power Limit (W)">
          <el-input placeholder="Enter power limit here" v-model="form.nvidia.pw"/>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="formVisibile = false">Cancel</el-button>
        <el-button type="primary" @click="emitSaved">Save</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/api/antos-api";

export default {
  name: "OCTemplate",
  props: {
    ocs: Array,
    launchEnabled: Boolean
  },
  data() {
    return {
      form: {
        name: '',
        isNvidia: true,
        amd: {
          cco: "",
          cc: "",
          cs: "",
          cv: "",
          mcv: "",
          mc: "",
          ms: "",
          mv: "",
          fan: "",
          pw: "",
          soc_f: "",
          vddmax: "",
          aggressive_undervolting: false
        },
        nvidia: {
          cco: "",
          mc: "",
          fan: "",
          pw: ""
        }
      },
      formVisibile: false
    }
  },
  methods: {
    emitSaved() {
      this.formVisibile = false
      this.$emit('saved', this.form)
    },
    edit(index) {
      this.form = this.ocs[index].config
      this.formVisibile = true
    }
  },
  mounted() {
    if(this.launchEnabled) {
      api.get_rig_config(this.$route.params.rig).then(resp => {
        this.ocs.forEach(value => {
          if(resp.data.oc_template === value._id.$oid) {
            value.inUse = true
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.newBtnRow {
  margin-bottom: 10px;
}

.oc-row {
  background-color: white;
  text-align: left;
  padding: 0 10px;
  align-items: center;
  margin-bottom: 10px;
}

.oc_form {
  text-align: left;
}
</style>